import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/login'
import Layout from '../views/layout'
import Home from '../views/home'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/layout',
    component: Layout,
    children:[
      {path: '', name: 'home', component: Home},
      {path: 'blogs', name: 'blog', component: () => import("../components/blogs")},
      {path: 'comment', name: 'comment', component: () => import("../components/comment")},
      {path: 'images', name: 'images', component: () => import("../components/images")},
      {path: 'message', name: 'message', component: () => import("../components/message")},
      {path: 'tags', name: 'tags', component: () => import("../components/tags")},
      {path: 'types', name: 'types', component: () => import("../components/types")},
      {path: 'user', name: 'user', component: () => import("../components/user")},
      {path: 'releaseBlogIndex', name: 'releaseBlogIndex', component: () => import("../components/releaseBlog")},
      {path: 'aboutIndex', name: 'aboutIndex', component: () => import("../components/about")},
    ]
  },

]

const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  base: '/blogAdmin/',
  routes
})
//路由导航守卫
//to：要访问的路由页面信息
//from：从哪里来
//next：放行
router.beforeEach((to,from,next)=>{
  //校验用户是否登录从而是否放行
  //获取用户信息
  const user=JSON.parse(sessionStorage.getItem("user"))
  if(to.path!=='/'){
    if(user){//以登录正常访问
      next()
    }else {//未登录跳转到登录页面
      next('/')
    }
  }else {
    //登录页面，正常允许通过
    next()
  }
})

export default router

