<!--文章ecahrts图组件-->
<template>
  <div id="blogEcahrtsbox" class="a" :style="{width: '400px', height: '300px',}"></div>
</template>

<script>
// 引入基本模板
import request from "../../utils/request";

var echarts = require('echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

export default {
  name: "blogEcahrts",
  components: {},
  data() {
    return {
    }
  },
//页面加载
  created() {
  },
//可以访问DOM元素
  mounted() {
    this.bLine();
  },
//方法集合
  methods: {
    bLine(){
      request.get("/blog/selectMostBlog").then(res=>{
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('blogEcahrtsbox'))
        // 绘制图表
        myChart.setOption({
          title: {
            text: '浏览量最多的6篇文章',
            left: 'center'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: res.data.data.barEchartsVo.name,
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '浏览量',
              type: 'bar',
              barWidth: '60%',
              data: res.data.data.barEchartsVo.value
            }
          ]
        });
      })
    },
  },
}
</script>

<style scoped>
</style>
        
