<!--分类ecahrts图组件-->
<template>
  <div class="typeEcahrtsbox" id="typeEcahrtsbox">

  </div>
</template>

<script>
// 引入基本模板
import request from "../../utils/request";

var echarts = require('echarts')
// 引入柱状图组件
require('echarts/lib/chart/pie');//饼图
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

export default {
  name: "typeEcahrts",
  components: {},
  data() {
    return {
    }
  },
//页面加载
  created() {
  },
//可以访问DOM元素
  mounted() {
    this.typeLine()
  },
//方法集合
  methods: {
    typeLine(){
      request.get("/type/selectMostBlogType").then(res=>{
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('typeEcahrtsbox'))
        // 绘制图表
        myChart.setOption({
          title: {
            text: '文章数最多的6个分类',
            left: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          toolbox: {
            show: true,
            feature: {
              mark: { show: true },
              dataView: { show: true, readOnly: false },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          series: [
            {
              name: '分类详情',
              type: 'pie',
              radius: [10, 100],
              center: ['50%', '50%'],
              roseType: 'area',
              itemStyle: {
                borderRadius: 5
              },
              data: res.data.data.typePieEcharts
            }
          ]
        });
      })
    },
  },
}
</script>

<style scoped>
.typeEcahrtsbox{
  height: 300px;
  width: 400px;
}
</style>
        
