        <div class="exhibitBox" >
          <el-row >
            <el-col :span="12">
              <div class="iconBox" >
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-yonghu"></use>
                </svg>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="wordBox">
                <p>用户数量</p>
                <p>{{ userCount }}</p>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <el-row  class="row-bg" >
      <el-col :span="8" :xs="24" :sm="12" :md="12" :lg="8">
        <div class="exhibitBox" >
          <el-row >
