          </el-row>
        </div>
      </el-col>
    </el-row>
    <!--Ecahrts-->
    <el-row :gutter="5" style="margin-top: 10px">
      <el-col :span="8" :xs="24" :sm="12" :md="12" :lg="8" >
        <div class="ecahrtsBox" >
          <!--分类Ecahrts-->
          <type-ecahrts/>
        </div>
      </el-col>
      <el-col :span="8" :xs="24" :sm="12" :md="12" :lg="8">
        <div class="ecahrtsBox" >
          <!--标签Ecahrts-->
          <tag-ecahrts/>
        </div>
      </el-col>
      <el-col :span="8" :xs="24" :sm="12" :md="12" :lg="8">
