  </div>
</template>

<script>
import "../../utils/iconfont"
import request from "../../utils/request";
import typeEcahrts from "../../components/ecahrts/typeEcahrts";
import tagEcahrts from "../../components/ecahrts/tagEcahrts";
import blogEcahrts from "../../components/ecahrts/blogEcahrts";
export default {
  name: "HomeIndex",
  components: {
    typeEcahrts,
    tagEcahrts,
    blogEcahrts
  },
  data() {
    return {
      blogCount:'100',//文章数
      commentCount:'88',//评论数
      messageCount:'66',//留言数量
      userCount:'180',//用户数量
      tagCount:'100',//标签数量
      typeCount:'99',//分类数量
      imageCount:'33',//图片数量
    }
  },
//页面加载
  created() {
    this.loadBlogCount()
    this.loadCommentCount()
    this.loadMessageCount()
    this.loadUserCount()
    this.loadTagCount()
    this.loadTypeCount()
    this.loadImageCount()
  },
//可以访问DOM元素
  mounted() {
  },
//方法集合
  methods: {
    //获取文章数量
    loadBlogCount(){
      request.get("/blog/selectBlogCount").then(res=>{
        this.blogCount=res.data.data.blogCount
      })
    },
    //获取评论数量
    loadCommentCount(){
      request.get("/comment/selectCommentCount").then(res=>{
        this.commentCount=res.data.data.commentCount
      })
    },
    //获取留言数量
    loadMessageCount(){
      request.get("/message/selectMessageCount").then(res=>{
        this.messageCount=res.data.data.messageCount
      })
    },
    //获取用户数量
    loadUserCount(){
      request.get("/user/selectUserCount").then(res=>{
        this.userCount=res.data.data.userCount
      })
    },
    //获取标签数
    loadTagCount(){
      request.get("/tag/selectTagCount").then(res=>{
        this.tagCount=res.data.data.tagCount
      })
    },
    //获取分类数
    loadTypeCount(){
      request.get("/type/selectTypeCount").then(res=>{
        this.typeCount=res.data.data.typeCount
      })
    },
    //获取图片数
    loadImageCount(){
      request.get("/image/selectImageCount").then(res=>{
        this.imageCount=res.data.data.imageCount
      })
    },
  },
}
</script>

<style scoped>
.homeBox{
  height: 150px;
}
.exhibitBox{
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  text-align: center;
  height: 150px;
}
.ecahrtsBox{
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  height: 300px;
  width: 400px;
}
.iconBox{
  font-size: 60px;
  line-height: 150px;
}
.wordBox{
  padding-top: 50px;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
</style>
        
