<!--布局组件-->
<template>
  <el-container class="container-box" >
    <!-- 导航 width="auto"表示宽自适应-->
    <el-aside class="m" width="auto" >
      <Aside style="height: 100%" :is-collapse="isCollapse"></Aside>
    </el-aside>
    <el-container>
      <!-- 头部 -->
      <el-header class="box">
        <div>
          <i :class="{'el-icon-s-fold':!isCollapse,'el-icon-s-unfold':isCollapse}" @click="isCollapse=!isCollapse"></i>
        </div>
        <el-dropdown>
          <div style="display: flex;align-items: center">
            <el-avatar shape="square" :size="50" :src="user.avatar"></el-avatar>
            <span >
              {{ user.nickname}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="about">个人信息</el-dropdown-item>
            <!--加上.native 即可以执行点击事件-->
            <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <!-- 内容 -->
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </el-container>

</template>

<script>
import Aside from "./components/aside";

export default {
  name: "LayoutIndex",

  components: {
    Aside,
  },
  data() {
    return {
      isCollapse:false,//控制菜单展开关闭
      user:{}
    }
  },
//页面加载
  created() {
    //获取浏览器存储用户信息
    this.user=JSON.parse(sessionStorage.getItem("user"))
  },
//可以访问DOM元素
  mounted() {
  },
//方法集合
  methods: {
    //跳转到个人信息
    about(){
      if(this.$route.path==="/layout/aboutIndex"){
        this.$message.error("当前已经在个人信息页")
      }else {
        this.$router.push('/layout/aboutIndex')
      }
    },
    //退出方法
    logout(){
      this.$confirm('此操作将退出, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //清除用户信息
        sessionStorage.removeItem("user")
        //跳转到登录页面
         this.$router.push('/')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消退出'
        });
      });


    }
  },
}
</script>

<style scoped >
.container-box{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
}
.box{
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
}

</style>
        
