<!--登录组件-->
<template>
  <div>
    <el-container style="height: 100%">
      <el-main width="70%">

      </el-main>
      <el-aside width="30%">
        <div class="formBox">
          <el-form
              ref="ruleForm"
              :model="user"
              :rules="rules"
              class="demo-ruleForm"
          >
            <el-form-item  prop="username">
              <!--oninput="value=value.replace(/[^\d]/g,'')"表示只能输入数字-->
              <el-input
                  oninput="value=value.replace(/[^\d]/g,'')"
                  v-model="user.username"
                  placeholder="用户名6到9位数字"
                  prefix-icon="el-icon-user-solid"
              ></el-input>
            </el-form-item>
            <el-form-item  prop="password">
              <el-input
                  show-password
                  v-model="user.password"
                  placeholder="密码6到13位"
                  prefix-icon="el-icon-unlock"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import { login } from "@/api/user.js";
import router from "../../router";
export default {
  name: "Login",
  data() {
    return {
      user: {
        username:'',
        password:''
      },
      rules: {
        username: [
          {required: true, message: '用户名不能为空', trigger: 'blur',},
          {min: 6, max: 9, message: '用户名长度要在6到9位', trigger: 'blur',},
        ],
        password: [
          {required: true, message: '密码不能为空', trigger: 'blur',},
          {min: 6, max: 13, message: '密码长度要在6到13位', trigger: 'blur',},
        ],
      },
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        const _this=this;
        if (valid) {
         login(this.user).then(res=>{
          // console.log(res.data.data.user)
          // console.log(res.data.result)
           if(res.data.result){
             if(res.data.data.user.type){
               this.$message.success("登录成功！")
                //将用户信息存入浏览器
               sessionStorage.setItem("user",JSON.stringify(res.data.data.user))
               //路由跳转
                router.push({path:"/layout"})
             }else {
               this.$message.error("该用户没有登录权限！")
             }
           }else {
             this.$message.error("用户名或者密码错误！")
           }
         })

        } else {
          return false
        }
      })
    },
  },
}
</script>

<style scoped>
.el-aside {
  text-align: center;
  line-height: 200px;
  height: 100vh;
  background: #ccc;
}
.el-main {
  background: url(login.jpg) center center /
    cover no-repeat;
  text-align: center;
  line-height: 160px;
  height: 100vh;
}
.formBox{
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  height: 150px;
  width: 80%;
  margin: 20vh auto;
  padding: 50px 10px;
  background: white;
}
</style>
